.section-creation{
    @media screen and (min-width: 768px) {
      margin-bottom: 184px;
    }   
    
    .header-creation{
        margin: 48px 0 16px;
        @media screen and (min-width: 768px) {
            margin: 187px 0 80px;
            display: flex;
            justify-content: space-between;
          }
        h2{
            text-align: center;
            font-family: $font2;
            @include fontStyle(2rem, 300, 32px, 0);
          
            text-transform: uppercase;
    
            @media screen and (min-width: 768px) {
              
               text-align: left;
               @include fontStyle(3.063rem, 300, 48px, 0);
             }
        }
    }
    

    .card-container{
        display: flex;
        flex-direction: column;
        height: 1131px;
        justify-content: space-between;

        @media screen and (min-width: 768px) {

            flex-direction: row;
            flex-wrap: wrap;
            gap: 30px;
            height: 100%;
          }

          @media screen and (min-width: 1264px) {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-template-rows: 1fr 1fr;

          }
        li{
            &:hover{
               h4{
                color: $Black;
               }
            }
            position: relative;
            img{
                width: 100%;
                height: 120px;
                cursor: pointer;
                @media screen and (min-width: 768px) {
                    width: 256px;
                    height: 450px;
                  }
            &:hover{
                opacity: 0.5;
            }
            }

            h4{
                font-family: $font2;
                @include fontStyle(1.5rem, 300, 32px, 0);
                color: $White;
                position: absolute;
                bottom: 20px;
                left: 20px;
                text-transform: uppercase;
            }
        }
    }

    button{
        margin: 34px  auto 96px;
        border: 1px solid black;
        background: none;
        width: 157px;
        height: 40px;
        text-transform: uppercase;
        display: block;
        @include fontStyle(0.875rem, 300, 14px, 5px);
        transition: background 0.3s ease;
        @media screen and (min-width: 768px) {
            margin: 0;
          }
        &:hover{
            background: black;
            color: $White;
        }
    }
}