.section-hero{
    margin-top:  96px;
    text-align: center;
    position: relative;
    @media screen and (min-width: 768px) {
        display: flex;
        margin-top: 160px;
     }
    img{
        width: 327px;

        @media screen and (min-width: 768px) {
            width: 731px;
         }
    }

    .txt-hero{
        width: 279px;
        margin: auto;
        @media screen and (min-width: 768px) {
            position: absolute;
            z-index: 100;
            bottom: 0;
            right: 0;
            width: 541px;
            height: 317px;
            background-color: $White;
            padding:69px 0 0 69px ;
            text-align: left;
         }
        h2{
            font-family: $font2;
            @include fontStyle(2rem, 300, 32px, 0);
            margin: 48px 0 16px;
            text-transform: uppercase;
            @media screen and (min-width: 768px) {
                @include fontStyle(3.063rem, 300, 48px, 0);
             }
        }
        p{
            @include fontStyle(0.938rem,400, 25px, 0);
            opacity: 0.5;
        }
    }
   
}