footer{
    background-color: $Black;
}
.footer-content{
    width: 272px;
    color: $White;
    text-align: center;
    padding: 56px 0 56px;

    @media screen and (min-width: 768px) {
        padding: 46px 0 46px;
       width: 100%;
       display: flex;
       flex-direction: column;
       align-content: space-between;
       flex-wrap: wrap;
       height: 190px;
       row-gap: 25px;
       
    }   
    img.logo{
       margin-bottom: 32px;
       @media screen and (min-width: 768px) {
       margin-bottom: 0;
       }
    }

    nav{
        ul{
            height: 189px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            @include fontStyle(0.938rem,300, 25px, 0 );
            @media screen and (min-width: 768px) {
              flex-direction: row;
              height: 35px;
              width: 380px;
             }
      
        }
    }

    .media-content{
        display: flex;
        width: 144px;
        justify-content: space-between;
        margin:  48px auto 16px ;
        height: 34px;
        align-items: center;
        @media screen and (min-width: 768px) {
            margin: 0;
            align-self: flex-end;
        }

        li a {
            position: relative;
            transition: all 0.3 ease;
            &::after{
                transition: all 0.5s ease;
                position: absolute;
                content: "";
                height: 2px;
                width: 24px;
                background: white;
                bottom: -8px;
                left: 0;
                opacity: 0;
            
            }
        &:hover::after{
            opacity: 1;
        }
        }
      
    }

    p{
        @include fontStyle(0.938rem,300, 25px, 0 );
        opacity: 0.5;
    }

}