header{
    background: url("../assets/images/mobile/image-hero.jpg");
    background-size: cover;
    background-repeat: no-repeat;
  
    height: 650px;

    @media screen and (min-width: 768px) {
        background: url("../assets/images/desktop/image-hero.jpg");
        background-size: cover;
        background-repeat: no-repeat;
     }

    .content-header{
        padding-top: 40px;
        @media screen and (min-width: 768px) {
            padding-top: 64px;
         }
        .header-nav{
            display: flex;
            justify-content: space-between;
            align-items: center;
     
            position: relative;
          
            
            #burger{

                display: flex;
                flex-direction: column;
                justify-content: space-between;
                position: absolute;
                
                right: 0;
                height: 16px;
                width: 24px;
                cursor: pointer;
                
              
                &.active {
                     top: 20px;
                     transform:  translateY(-50%);
                     span{
                        position: absolute;
                        top: 0;
                     }
                    span:nth-child(1){
                        transform: rotate(45deg); 
                    }
                    span:nth-child(2){
                        display: none;
                    }
                    span:nth-child(3){
                        transform: rotate(-45deg);
                    }
                }
                span{
                    width: 24px;
                    height: 2px;
                    display: block;
                    background-color: $White;
                }
            }
        }
        
        nav{
            margin-top: 159px;
            text-transform: uppercase;
                @media screen and (min-width: 768px) {
                   margin-top: 0;
                   text-transform: capitalize;
                }
            ul{
                @include fontStyle(1.5rem,300,25px,0);       
                @media screen and (min-width: 768px) {
                    @include fontStyle(0.938rem,300,25px,0);  
                    flex-direction: row;
                    height: 25px;
                    width: 380px;
                }
            }
        }
        .hero-container { 
            margin-top: 163px;
            width: 100%;
            height: 196px;
            border: 2px solid white ;
            padding:24px ;
            @media screen and (min-width: 768px) {
                margin-top: 130px;
                max-width: 650px;
                height: 278px;
                padding:40px ;
            }

            h1{
                font-family: $font2;
                @include fontStyle(2.5rem,300,38px,0);
                color: $White;
                text-transform: uppercase;
                @media screen and (min-width: 768px) {
                    @include fontStyle(4.5rem,300,70px,0);
                }
        } }
       





    }

    
}
