
/*********** COLOR *************/
$White: hsl(0, 0%, 100%);
$Black: hsl(0, 0%, 0%);
$DarkGray: hsl(0, 0%, 55%);
$VeryDarGray: hsl(0, 0%, 41%);

/*********** FONT *************/
@font-face {
    font-family:"Alata";
    src: url(../assets/fonts/Alata-Regular.ttf);
}

@font-face {
    font-family:"Josefins";
    src: url(../assets/fonts/JosefinSans-VariableFont_wght.ttf);
}

$font1: Alata, sans-serif;
$font2: Josefins, sans-serif;


@mixin fontStyle($size,$weight,$lineSpace, $letterSpacing){
    font-size: $size;
    font-weight: $weight;
    line-height: $lineSpace;
    letter-spacing: $letterSpacing;
}

/********* General parameter *********/

*{
    padding: 0;
    margin: 0;
    font-size: 100%;
	font: inherit;
	vertical-align: baseline;
    box-sizing: border-box;
}

ol,ul{
    list-style: none;
}

a{
    text-decoration: none;
    color: inherit;
    &:visited{
        color: inherit;
    }
}

body{
    font-family: $font1;
}

img{
    object-fit: contain;
}

.wrapper{
    width: 100%;
    max-width: 327px;
    margin: 0 auto;

    @media screen and (min-width: 768px) {
        width: 90%;
        max-width: 1110px;
    }
}

.logo { width: 144px; 
    
    @media screen and (min-width: 768px) {
       width: 192px;
    }}

.nav{
    color: $White;
    ul{
    
        display: flex;
        flex-direction: column;
        height: 221px;
        justify-content: space-between;
        font-family: $font2;
        
        
        @media screen and (min-width: 768px) {
            flex-direction: row;
        }
        
        li a {
            position: relative;
            transition: all 0.3 ease;
            &::after{
                transition: all 0.5s ease;
                position: absolute;
                content: "";
                height: 2px;
                width: 24px;
                background: white;
                bottom: -8px;
                left: 0;
                opacity: 0;
            
            }
        &:hover::after{
            opacity: 1;
            left: 50%;
            transform: translateX(-50%);
        }
        }
    }
}